<template>
  <v-container>
    <v-overlay :value="updating">
        {{updating}}
      </v-overlay>
    
    <v-layout
      text-center
      wrap
    >
    <v-container>
    <v-alert v-if="isLive" type="info" dense>    ->   ->  </v-alert>    
    </v-container>
    <v-btn v-if="!isLive" color="primary" dark class="mb-2" @click="meta_update">   </v-btn>
    <v-btn v-if="isLive" color="primary" dark class="mb-2" @click="meta_update_live">   (Live)</v-btn>
      <v-tabs
        fixed-tabs
        background-color="indigo"
        dark
      >
        <v-tab v-for="tab_item in tabs" :key="tab_item.seq" @click.prevent="get_tab_link(tab_item)">
          {{tab_item.title}}
        </v-tab>
      </v-tabs>
      <router-view></router-view>
    </v-layout>
  </v-container>
</template>

<script>
import config from '../../src/config/config';
import { mapActions } from 'vuex';

const dateFormat = require('dateformat');
const {exportPurgeTxt} = require("../util/fileutil");
export default {
  name: '',
  created() {
  },
  computed: {
    isLive(){
      return config.isLive;
    },
  },
  methods: {
    ...mapActions([
      'META_PRODUCT_UPDATE',
      'META_PRODUCT_UPDATE_LIVE',
      'OPERATE_SERVER'
    ]),
    get_tab_link(tab_item) {
      this.$router.push(`/product_tab/${tab_item.path}`);
    },
    async meta_update() {
      this.updating = "      "
      await this.META_PRODUCT_UPDATE();
      this.updating = " ...."
      await this.OPERATE_SERVER({server: "test", operation: "stop"});
      this.updating = " ...."
      await this.OPERATE_SERVER({server: "test", operation: "start"});
      this.updating = ""
    },
    async meta_update_live() {
      this.updating = "      "
      let result = await this.META_PRODUCT_UPDATE_LIVE();
      exportPurgeTxt(result.purge_list, `ProductMeta_${dateFormat(new Date(), 'yyyymmdd_HHMM')}`);
      this.updating = ""
    },
    
  },
  data() {
    return {
      updating: "",
      seq_title: "",
      tabs: [
        {type: "product", title: " ", path:"product"},
        {type: "product_adult", title: " ", path:"product_adult"}
      ],
    }
  }
};
</script>
